import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { of } from 'rxjs';
import { map, catchError, switchMap } from 'rxjs/operators';

import { ProductService } from '../../core/services/product.service';
import * as ProductActions from '../actions/product.actions';

@Injectable()
export class ProductEffects {
  
  loadProducts$ = createEffect(() =>
    this.actions$.pipe(
      ofType(ProductActions.loadProducts),
      switchMap(({ params }) =>
        this.productService.getProducts(params).pipe(
          map(paginatedResponse => ProductActions.loadProductsSuccess({ paginatedResponse })),
          catchError(error => of(ProductActions.loadProductsFailure({ error })))
        )
      )
    )
  );

  loadProductById$ = createEffect(() =>
    this.actions$.pipe(
      ofType(ProductActions.loadProductById),
      switchMap(({ productId }) =>
        this.productService.getProductById(productId).pipe(
          map(product => ProductActions.loadProductByIdSuccess({ product })),
          catchError(error => of(ProductActions.loadProductByIdFailure({ error })))
        )
      )
    )
  );

  loadCategories$ = createEffect(() =>
    this.actions$.pipe(
      ofType(ProductActions.loadCategories),
      switchMap(() =>
        this.productService.getCategories().pipe(
          map(categories => ProductActions.loadCategoriesSuccess({ categories })),
          catchError(error => of(ProductActions.loadCategoriesFailure({ error })))
        )
      )
    )
  );

  constructor(
    private actions$: Actions,
    private productService: ProductService
  ) {}
}
